body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
	background-color: #f0f0f0;
	-webkit-transition: 1s all linear;
	transition: 1s all linear;
}

a {
	text-decoration: none;
	color: #444;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

a:hover {
	text-decoration: none;
	color: orange;
}

b {
	color: black;
}

h1, h2, h3, h4, h5, h6, p, ul{
	text-align: left;
}

aside {
	position: absolute;
	padding-top: 2em;
	padding-bottom: 40px;
	padding-left: 2em;
	padding-right: 2em;
	width: 20%;
	position: fixed;
	bottom: 0;
	top: 0;
	left: 0;
	overflow-y: scroll;
	background-color: #fff;
	box-shadow: 4px 0px 4px 0px #ccc;
}

.head-img {
	width: 100%;
	padding-top: 100%;
	background-image: url(/home/static/media/head.4bb01a8b.jpg);
	background-size: cover;
	border-radius: 50%;
}

.header-line {
	margin-top: 1.5em;
	width: 100%;
	height: 4px;
	border-radius: 2px;
	background-color: #354AB3;
	margin-bottom: 12px
}

.header {
	margin-top: 2em;
	text-align: right;
}

.header-item {
	font-size: 20px;
	margin-bottom: 4px;
}

.text-white {
	color: #fff;
}

.block {
	position: relative;
	width: 100%;
	border-radius: 8px;
	margin-bottom: 12px;
	padding: 2em;
	background-color: #fff;
	box-shadow: 5px 6px 5px #ccc;
}

.under-line {
	width: 75px;
	height: 4px;
	border-radius: 2px;
	/*background-color: #41D4FF;*/
	background-color: #354AB3;
	margin-bottom: 12px
}

.block-item {
	padding-left: 2em;
}

.margin-bottom {
	margin-bottom: 1em;
}

/*###################################################*/

/* Part 1, top region and intro */

/*###################################################*/

.container {
	position: absolute;
	left: 20%;
	width: 80%;
	z-index: -1;
}

.background {
	position: relative;
	height: 400px;
	background-size: cover;
	padding: 12px;
	/*box-shadow: 0px 0px;*/
}

.name-line {
	width: 200px;
	height: 4px;
	border-radius: 2px;
	background-color: #354AB3;
	margin-bottom: 12px
}

.triangle {
	position: absolute;
	top: 0px;
	left: 0px;
	height: 100%;
	width: 100%;
	border-left: 930px solid transparent;
	border-top: 400px solid #354AB3;
}

.intro {
	font-family: "Quicksand", Arial, sans-serif;
	position: absolute;
	padding: 24px;
	padding-bottom: 0px;
	bottom: 12px;
	background-color: transparent;
}

.intro-list li {
	color: #777;
}

.icon-custom {
	color: #777;
	font-size: 24px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.icon-custom:hover {
	color: orange;
}

/*###################################################*/

/* Part 2, Personal Blocks

/*###################################################*/

.row-block {
	padding: 0px 15px 0px 15px;
	margin-bottom: 12px;
}

.col-block-left {
	padding: 0px 4px 0px 0px;
}

.col-block-right {
	padding: 0px 0px 0px 4px;
}

.interests {
	width: 100%;
	height: 100%;
	background-color: #fff;

}

.check-icon {
	color: #22D701;
}

.comment {
	font-size: 14px;
}

.margin-bottom {
	margin-bottom: 24px;
}

.footer {
	width: 100%;
	height: 350px;
	/*margin-bottom: 12px;*/
	/*background-color: #354AB3;*/
	background-color: transparent;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

